import {Box,Image,HStack,VStack,Text,Heading} from "@chakra-ui/react"
import Profilepic from "../Images/portprofile.png"
import linkdin from "../Images/linkedin.png"
import github from "../Images/github(3).png"
import msg from "../Images/mailimg.png"
import { Show } from '@chakra-ui/react'
export default function About(){

    return (
        <>
        <Show above="700px">
        <HStack  justifyContent={"space-evenly"} boxSizing="border-box"  w={"98%"} m="auto" h={"500px"} mt={"30px"} borderRadius="10px" bgColor="#d62323 ">
            
            <Box  w="25%" boxShadow= "rgba(0, 0, 0, 0.24) 0px 3px 8px" >
            <Image w={"90%"} m="auto" borderRadius={"10px"}  src={Profilepic}  />
            </Box>
            <Box w="40%">
            <VStack>
            <Box    >
                <Heading >Hi..</Heading>
                <Heading color="">I am Abhishek Pal</Heading>
                <Text mt="10px" fontSize={"23px"} color="white">"Full stack web developer with a passion for creating intuitive and functional web applications using a variety of technologies including HTML, CSS, JavaScript, and modern frameworks such as React and Node.js.</Text>
            </Box>
            <HStack justify={"center"}>
                <Image w="10%" src={linkdin} />
                <Image w="10%"  src={github}/>
                <Image w="10%"  src={msg}/>
            </HStack>
            </VStack>
            </Box>
        </HStack>

        </Show>
        <Show below="700px">
        <VStack justifyContent={"space-evenly"} boxSizing="border-box"  w={"98%"} m="auto" h={"auto"} mt={"30px"} borderRadius="10px" bgColor="#d62323 ">
            
            <Box mt="20px"   w="55%" boxShadow= "rgba(0, 0, 0, 0.24) 0px 3px 8px" >
            <Image w={"90%"} m="auto" borderRadius={"10px"}  src={Profilepic}  />
            </Box>
            <VStack>
            <Box w="80%"  >
                <Heading >Hi..</Heading>
                <Heading color="">I am Abhishek Pal</Heading>
                <Text mt="10px" fontSize={"23px"} color="white">"Full stack web developer with a passion for creating intuitive and functional web applications using a variety of technologies including HTML, CSS, JavaScript, and modern frameworks such as React and Node.js.</Text>
            </Box>
            <HStack justify={"center"}>
                <Image w={"10%"} src={linkdin} />
                <Image w={"10%"} src={github}/>
                <Image w={"10%"} src={msg}/>
            </HStack>
            </VStack>
        </VStack>
        </Show>
        </>
    )
}